import { useDispatch, useSelector } from 'react-redux';
import { actionSetQueryResult } from '../data/action';
import { useUiDashboardQuery, useUiDashboardQueryIsLoading } from './useUi';
import { queryOql } from '../data/repository';
import { useFormatDate, useParseDate } from '@octavius-rocks/ui';
import { useEffect, useState } from 'react';
import { useDashboardQueryArgs } from './useQueryArgs';
import { mapValues } from '../utils/oql.js';


/**
 * get data
 */
export const useQueryResult = (key) => {
	return useSelector(({query})=> query[key] || {})
}

/**
 * equivalent to useState
 * @param {string} key store key
 */
export const useQueryState = (key)=>{
	const dispatch = useDispatch()
	const result = useQueryResult(key)
	return [
		result.value || [],
		(oql) => {
			return queryOql(oql).then(result =>{
				dispatch(actionSetQueryResult(key, {
					oql:oql,
					value: mapValues(result.value),
					error: result.error
				}))
			})
		},
		result,
	]
}

/**
 * use top contents store
 * @param {string} key store key
 * @param {object} props for oql
 * @param {string} props.event_type
 * @param {Date} props.from
 * @param {Date} props.to
 * @param {integer} props.numberOfItems
 */
export const useQueryStatePageviews = (key, props = {})=>{
	const [value, fetch] = useQueryState('pageviews_'+key)
	const formatDate = useFormatDate();
	useEffect(()=>{
		const {
			event_type = "pageview",
			from,
			to,
			aggregation = "day",
		} = props;
		fetch({
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"timestamp",
			],
			conditions:{
				entries:[
					{
						"field":"event_type",
						"value": event_type,
						"compare":"="
					},
					{
						"field":"timestamp",
						"value": formatDate(from, "yyyy-MM-dd HH:mm:ss"),
						"compare":">=",
					},
					{
						"field":"timestamp",
						"value": formatDate(to, "yyyy-MM-dd HH:mm:ss"),
						"compare":"<=",
					},
				],
				relation: "AND"
			},
			aggregation: aggregation,
			order_by:[ {field:"timestamp", direction: "ASC"}]
		});
	}, [key, JSON.stringify(props)])

	return value
}


/**
 * use top contents store
 * @param {string} key store key
 * @param {object} props for oql
 * @param {string} props.event_type
 * @param {Date} props.from
 * @param {Date} props.to
 * @param {integer} props.numberOfItems
 */
export const useQueryStateTopContentIds = (key, props = {})=>{
	const [value, fetch] = useQueryState('topContents_'+key)
	const formatDate = useFormatDate()
	useEffect(()=>{
		const {
			event_type = "pageview",
			from = new Date(Date.now() - 1000* 60 * 60 * 24 * 7),
			to = new Date(),
			numberOfItems = 10,
		} = props;
		fetch({
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"content_id",
				"content_type",
			],
			conditions:{
				entries:[
					{
						"field":"event_type",
						"value": event_type,
						"compare":"="
					},
					{
						"field":"timestamp",
						"value": formatDate(from, "yyyy-MM-dd HH:mm:ss"),
						"compare":">=",
					},
					{
						"field":"timestamp",
						"value": formatDate(to, "yyyy-MM-dd HH:mm:ss"),
						"compare":"<=",
					},
				],
				relation: "AND"
			},
			order_by:[ {field:"hits", direction: "DESC"}],
			group_by: [ "content_id"],
			limit: numberOfItems,
			page: 1,
		})
	}, [key, JSON.stringify(props)])

	return value
}

