// --------------------------------------------------
// hard coded stores
// --------------------------------------------------

/**
 * list of available stores
 */
export const useQueryResultPageviews = ()=> useQueryResult('pageviews')
export const useQueryResultTopContentIds = ()=> useQueryResult('topContentIds')
export const useQueryResultTopReferer = ()=> useQueryResult('topReferer')
export const useQueryResultScreenSizes = ()=> useQueryResult('screenSizes')

/**
 * collect conditions of query ui
 */
export const useCollectQueryConditions = ()=>{
	const [args] = useUiDashboardQuery();
	const formatDate = useFormatDate();

	return ()=>{
		let entries = [];
		try{
			entries = [
				{
					"field":"timestamp",
					"value": formatDate(args.from, "yyyy-MM-dd HH:mm:ss"),
					"compare":">=",
				},
				{
					"field":"timestamp",
					"value": formatDate(args.to, "yyyy-MM-dd HH:mm:ss"),
					"compare":"<=",
				},
			];
		} catch(e){
			console.error(e);
			entries = [
				{
					"field":"timestamp",
					"value": formatDate(args.from, "yyyy-MM-dd HH:mm:ss"),
					"compare":">=",
				},
				{
					"field":"timestamp",
					"value": formatDate(args.to, "yyyy-MM-dd HH:mm:ss"),
					"compare":"<=",
				}
			];
		}
		if(args.conditions){
			entries.push(args.conditions);
		}

		return {
			entries,
			relation: "AND",
		}
	}
}

/**
 * check for aggregation
 */
export const useCollectQueryAggregation = ()=>{
	const [args] = useUiDashboardQuery()
	return ()=> args.aggregation
}

/**
 * fetch data
 */
export const useSubmitDashboardQuery = ()=>{
	const dispatch = useDispatch()
	const [ args ] = useUiDashboardQuery() // args for submission
	const [_, setQueryArgs] = useDashboardQueryArgs() // save to config that is persistet
	const [isLoading, setIsLoading] = useUiDashboardQueryIsLoading()

	const parseDate = useParseDate();
	const getConditions = useCollectQueryConditions();
	const getAggregation = useCollectQueryAggregation();
	const submit = ()=>{

		setIsLoading(true)

		setQueryArgs(args)

		const conditions = getConditions()

		// pageview per time
		const pageviewsOql = {
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"timestamp",
			],
			conditions,
			aggregation: getAggregation(),
			order_by:[ {field:"timestamp", direction: "ASC"}]
		}
		queryOql(pageviewsOql).then(result =>{
			setIsLoading(false)

			// fill empty days
			const from = args.from
			const to = args.to



			dispatch(actionSetQueryResult("pageviews", {
				oql:pageviewsOql, 
				value: mapValues(result.value)
			}))
		})

		// top content ids
		const topContentIdsOql = {
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"content_id",
				"content_type",
			],
			conditions,
			order_by:[ {field:"hits", direction: "DESC"}],
			group_by: [ "content_id"],
			limit: 10,
			page: 1,
		}
		queryOql(topContentIdsOql).then(result=>{
			if(!result.error){
				dispatch(actionSetQueryResult("topContentIds", {
					value: mapValues(result.value), 
					oql: topContentIdsOql
				}))
			}
		})
		// TODO: fetch missing post ids

		// pageview for referer
		const topRefererOql = {
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"referer_domain",
			],
			conditions,
			order_by:[ {field:"hits", direction: "DESC"}],
			group_by: [ "referer_domain"],
			limit: 10,
			page: 1,
		}
		queryOql(topRefererOql).then(result=>{
			if(!result.error){
				dispatch(actionSetQueryResult("topReferer", {
					value: mapValues(result.value), 
					oql: topRefererOql
				}))
			}
		})

		// screen sizes
		const screenSizesOql = {
			fields:[
				{
					field: "hits",
					operations: ["sum"],
					as: "hits"
				},
				"breakpoint",
			],
			conditions,
			order_by:[ {field:"hits", direction: "DESC"}],
			group_by: [ "breakpoint"],
		}
		queryOql(screenSizesOql).then(result=>{
			if(!result.error){
				dispatch(actionSetQueryResult("screenSizes", {
					value: mapValues(result.value),
					oql: screenSizesOql
				}))
			}
		})
	}

	return [
		isLoading,
		submit,
	]
}