/**
 * Query contents with oql
 * @param {object} oql
 */
export const queryOql = (oql) => {
	return new Promise(async (resolve)=>{
		waitForGet(WP_OctaviusRocks_Admin.api,(get)=>{
			resolve(get.query(oql));
		})
	});
};

