/**
 * in memory cache of fetched contents
 * SO NEVER CALL TO OFTEN
 */
const CACHED_CONTENTS = {};

/**
 * get post objects
 * @param contentIds
 * @return {Promise}
 */
export const getContents = (contentIds) => {
	return new Promise(resolve => {
		const cachedKeys = Object.keys(CACHED_CONTENTS);
		const missingContentIds = contentIds.filter( id => !cachedKeys.includes(id));
		if(missingContentIds.length > 0){
			fetchContents(missingContentIds).then(()=>{
				resolve(contentIds.map(id => CACHED_CONTENTS[id]));
			});
		} else {
			resolve(contentIds.map(id => CACHED_CONTENTS[id]));
		}
	});
};

/**
 * fetch contents from wordpress ajax endpoint
 * @param ids
 * @return {Promise<boolean>}
 */
const fetchContents = (ids) => postRequest(
		'oc_get_contents',
		{content_ids: ids}
	)
	.then(list => {
		const isSuccess = (typeof list === typeof []);
		if(isSuccess){
			for (let item of list){
				CACHED_CONTENTS[item.ID] = item;
			}
		}
		return isSuccess;
	});

